import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/login';
import SignUp from '@/components/signup';

Vue.use(Router);

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
  },
  {
    path: '/sign-up',
    name: 'Signup',
    component: SignUp,
  },
];

export default routes;



