
<template>
  <div>
    <div class="loginBox">
      <h3>Signup Box</h3>

        <div class="form-group">
          <input type="name" class="form-control" id="exampleUsername" v-model="username" placeholder="username">
        </div>
        <div class="form-group">
          <input type="name" class="form-control" id="exampleInputName" v-model="name" placeholder="name">
        </div>
        <div class="form-group">
          <input type="email" class="form-control" id="exampleInputEmail" v-model="email" placeholder="email">
        </div>
        <div class="form-group">
          <input type="password" class="form-control" id="exampleInputPassword1" v-model="password" placeholder="password">
        </div>
        <div class="checkbox">
          <label>
            <input type="checkbox"> Not a robot?
          </label>
        </div>
        <button class="btn btn-primary" @click="submitData">Submit</button>

    </div>
  </div>
</template>
 
<script>
/* eslint-disable */
// import AWSCognito from 'amazon-cognito-identity-js';
import {CognitoUserPool, CognitoUser, AuthenticationDetails, CognitoUserAttribute} from 'amazon-cognito-identity-js'

export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      username: '',
      name: '',
      password: '',
      email:'',
    };
  },
  methods:{
    submitData(){
      
      const showError = (message) => {
        this.$toasted.show('Error:',message);
        console.log('Err message',message)
      }
      var poolData = { 
          UserPoolId : 'ap-south-1_hJlZ9C71R',
          ClientId : '512saf5a9dj8uod0dqg326ihg3'
      };
      var userPool = new CognitoUserPool(poolData);

      var attributeList = [];
      
      var dataEmail = {
          Name : 'email',
          Value : this.email
      };
      var dataName = {
          Name : 'name',
          Value : this.name
      };
      var attributeEmail = new CognitoUserAttribute(dataEmail);
      var attributeName = new CognitoUserAttribute(dataName);

      attributeList.push(attributeEmail);
      attributeList.push(attributeName);
      userPool.signUp(this.username, this.password, attributeList, null, function(err, result){
          if (err) {
            showError(err.message);
            return;
          }
          cognitoUser = result.user;
          console.log('username:',this.username,this.password);
          console.log('user name is ' + cognitoUser.getUsername());
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
 background-color: #3D3D3D;
 background-image: repeating-linear-gradient(45deg, transparent, transparent 3px, rgba(76,76,76,.5) 3px, rgba(76,76,76,.5) 5px);
 color: #333333;
}

.loginBox {
 background: rgba(238, 238, 238, .75);
 width: 100%;
 max-width: 20em;
 padding: 1em;
 margin: 2em auto;
 border-radius: .4em;
 box-shadow: 1px 1px 4px 0px rgba(50, 50, 50, 0.5);
}

.loginBox h3 {
 margin-top: 0;
}

.ulinks {
 margin: 1em 0;
}

</style>



